.selectedPage{
    font-weight: 900;
    font-size: 25px;
    margin: 5px;
}
.pagesItem{
    font-size: 20px;
    margin: 5px;
    cursor: pointer;
}
.pages_wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
}
.arrow{
    width: 30px;
}
.arrow:hover{
    cursor: pointer;
}
.left_arrow{
    transform: rotate(180deg);
}